* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.filter {
  width: 450px;
  height: 670px;
  margin: 30px 90px;
  border-radius: 20px;
  box-shadow: 3px 16px 25px -7px rgba(0, 0, 0, 0.35);
}
.content {
  padding: 20px;
}
.title {
  padding: 20px;
  font-size: 2rem;
  font-weight: 350;
  text-align: center;
}

.name {
  padding: 5px;
  font-size: 1.25rem;
  font-weight: 300;
  margin-left: 20px;
}
